declare global {
  interface Array<T> {
    getNextCyclicEntry(I: number): T;
    getPreviousCyclicEntry(I: number): T;
  }
}

Array.prototype.getNextCyclicEntry = function(startIndex: number) {
  if (startIndex === this.length - 1) {
    return this[0];
  } else {
    return this[startIndex + 1];
  }
};

Array.prototype.getPreviousCyclicEntry = function(i: number) {
  if (i === 0) {
    return this[this.length - 1];
  } else {
    return this[i - 1];
  }
};
